export default [
  {
    path: "/guidepage",
    component: () => import("../components/public/guidepage.vue")
  },
  {
    path: "/advertisement",
    component: () => import("../components/public/advertisement.vue"),
    beforeEnter: (to, from, next) => {
      // console.log(to,from);
      // 1.通过存储过程，判断存储中是否存在数据
      if (localStorage.advertpage) {
        // 2.有参数值，进入过引导页，直接跳转广告页
        next();
      } else {
        // 3.没有参数值，进入引导页
        next("/guidepage");
      }
    }
  },
  {
    path: "/exchangeCoupon",
    component: () => import("../components/public/exchangeCoupon.vue")
  }
];
