export default {
  namespaced: true,
  state: {
    list: []
  },
  mutations: {
    onClickButton(state, obj) {
      let index = state.list.findIndex(val => val.id == obj.id);
      if (index != "-1") {
        // 存在数据，数量加一
        state.list[index].num = parseInt(state.list[index].num) + 1;
      } else {
        // 不存在数据，数据添加购物车
        state.list.push(obj);
      }
    }
  },
  getters: {
    num(state) {
      return state.list.length;
    },
    total(state) {
      var num = 0;
      state.list.forEach(item => {
        if (item.bool) {
          num += item.num * item.price;
        }
      });
      num *= 100;
      return num;
    }
  },
  actions: {}
};
